.chart_con {
  box-shadow: 0px 2px 3px 0px rgb(152, 99, 202);
  -webkit-box-shadow: 0px 2px 3px 0px rgb(152, 99, 202);
  -moz-box-shadow: 0px 2px 3px 0px rgb(152, 99, 202);
  padding: 1rem;
  margin: 0 1rem;
  width: 30%;
  height: 22rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}
.chart_head {
  font-size: 1.7rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

@media (max-width: 1024px) {
  .chart_con {
    width: 100%;
    padding: 0;
    margin: 1rem 0;
  }
}
