.dashboard {
  position: absolute;
  margin-top: 5rem;
  transition: 0.5s;
  transition-timing-function: ease-out;
  transition-property: all;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
}
.dashboardFull {
  margin-left: 18rem;
  width: calc(100% - 18rem);
}
.dashboardHalf {
  margin-left: 6rem;
  width: calc(100% - 6rem);
}
.sec1 {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 539px) {
  .dashboard {
    margin: 0;
    margin-top: 9rem;
    align-items: center;
    width: 100%;
  }
  .dashboardFull {
    margin: 0;
    margin-top: 9rem;
  }
  .dashboardHalf {
    margin: 0;
    margin-top: 9rem;
  }
  .sec1 {
    flex-direction: column;
  }
}
